import { useState } from "react";

import NaughtyChecked from "../components/NaughtyChecked";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";

function LetterToSanta() {
  const [howBehaved, setHowBehaved] = useState("");

  return (
    <section>
      <RadioGroup value={howBehaved}>
        <Radio
          value="naughty"
          onChange={event => setHowBehaved(event.target.value)}
        >
          Naughty
        </Radio>
        <Radio
          value="nice"
          onChange={event => setHowBehaved(event.target.value)}
        >
          Nice
        </Radio>
        <Radio
          value="veryNice"
          onChange={event => setHowBehaved(event.target.value)}
        >
          Very Nice
        </Radio>
      </RadioGroup>
      {howBehaved === naughty && <NaughtyChecked />}
    </section>
  );
}

export default LetterToSanta;
